exports.install = function(Vue,options){
// methodAPIdata
  Vue.prototype.jsp = function(url,data){
        // baseUrl
       // const baseUrl = 'http://192.168.191.1:8000/';
       // let url =    `${baseUrl}${method}?`;
        // jsonpgetparams
        for(let item in data){
             url += `&${item}=${data[item]}`;
        }
        return this.$jsonp(url);
    };
}



